<template>
  <div class="page">
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
    <ctest :tittle="tittle"></ctest>
    
    <div class="ddd" :style="{'width': chartsWidth+'px','height':chartsHeight+'px'}">
        <canvas id="aaa" class="charts" />
    </div>

    
    <!-- <el-button class="el-btn" :style="{'left': elBtnLeft+'px'}" type="primary" plain></el-button> -->
    <solar :solarWidth="solarWidth" :solarHeight="solarHeight"></solar>
    <div class="nav-footer" v-if='isPc'></div>
    
  </div>
</template>

<script>
import ctest from 'common/c-test'
import solar from 'common/solar-system'
//import uCharts from 'common/u-charts'
// import uCharts from '../utils/u-charts';
import uCharts from '@qiun/ucharts'
//Vue.use(uCharts);
var uChartsInstance = {};

export default {
  data () {
    return {
      isPc:true,
      tittle:'',
      solarWidth:100,
      solarHeight:80,
      elBtnLeft:100,
      cWidth: 300,
      cHeight: 500,
      chartsWidth:750,
      chartsHeight:500,
    }
  },
  created(){
    this.solarWidth = this.pubMethods.getScreenWidth()
    this.chartsWidth = this.pubMethods.getScreenWidth()
    //this.cWidth = this.pubMethods.getScreenWidth()
    this.solarHeight = this.pubMethods.getScreenHeight()
    this.elBtnLeft = this.pubMethods.getScreenWidth()/2 - 40
     
  },
  components:{
    ctest,
    solar
  },
  computed: {
    
  },
  mounted(){
    this.getServerData()
  },
   onReady() {
           console.log('')

   
  },
  
  methods: {

    getServerData() {
      //
      setTimeout(() => {
        //
        let res = {
            categories: ["2016","2017","2018","2019","2020","2021"],
            series: [
              {
                name: "",
                data: [35,36,31,33,13,34]
              },
              {
                name: "",
                data: [18,27,21,24,6,28]
              }
            ]
          };
        this.drawCharts('aaa', res);
      }, 500);
    },
    drawCharts(id,data){
      const ctx = document.getElementById(id).getContext("2d");
     //const ctx = this.$refs.aaa.getContext("2d");
      console.log('-->'+ctx)
      
      uChartsInstance[id] = new uCharts({
        type: "column",
        context: ctx,
        width: this.cWidth,
        height: this.cHeight,
        categories: data.categories,
        series: data.series,
        animation: true,
        background: "#FFFFFF",
        color: ["#1890FF","#91CB74","#FAC858","#EE6666","#73C0DE","#3CA272","#FC8452","#9A60B4","#ea7ccc"],
        padding: [15,15,0,5],
        legend: {},
        xAxis: {
          disableGrid: true
        },
        yAxis: {
          data: [
            {
              min: 0
            }
          ]
        },
        extra: {
          column: {
            type: "group",
            width: 30,
            activeBgColor: "#000000",
            activeBgOpacity: 0.08
          }
        }
      });
    },
    tap(e){
      uChartsInstance[e.target.id].touchLegend(e);
      uChartsInstance[e.target.id].showToolTip(e);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@import "~styles/index.less";
@import "~styles/variable.less";
.nav-footer {
    position: fixed;
    left: 0px;
    bottom: 0px;
    width: 100%;
    .h(98);
    background: @tab-general-bg-color;
    display: block;
    footer {
      font-size: 0;
      border-top: 1px solid #E7E7E7;
      border-bottom: 1px solid #F8F8F8;
      .pt(10);
      line-height: 1.2;
      width: 100%;
      .h(98);
      display: flex;
      a {
        text-decoration: none;
        color: @base-font-color;
        -webkit-box-flex: 1;
        -moz-box-flex: 1;
        -webkit-flex: 1;
        -ms-flex: 1;
        flex: 1;
        text-align: center;
        box-sizing: border-box;
        display: block;
        .fs(22);
        .tab-0, .tab-1, .tab-2, .tab-3 {
          display: inline-block;
          .h(42);
          .w(42);
          background-size: cover;
        }
        .tab-0 {
          background-image: url("../assets/imgs/index-noselect.svg");
        }
        .tab-1 {
          background-image: url("../assets/imgs/note-noselect.svg");
        }
        .tab-2 {
          background-image: url("../assets/imgs/print-noselect.svg");
        }
        .tab-3 {
          background-image: url("../assets/imgs/dy-noselect.svg");
        }
        &:hover {
          text-decoration: none;
        }
        &.router-link-active.active {
          .tab-0 {
            background-image: url("../assets/imgs/index-select.svg");
          }
          .tab-1 {
            background-image: url("../assets/imgs/note-select.svg");
          }
          .tab-2 {
            background-image: url("../assets/imgs/print-select.svg");
          }
          .tab-3 {
            background-image: url("../assets/imgs/dy-select.svg");
          }
        }
      }
      p {
        margin: 0;
      }
    }
  }
.ddd{
  overflow-x: scroll;
}
.charts{
    width: 750px;
    height: 500px;
    z-index: 999;
  }
  .el-btn{
    position: absolute;
    top:10px;
    z-index: 900;
    color: #E7E7E7;
    background: linear-gradient(45deg, #0078ee, #FFFFFF,#ff5500,#FFF00D,#FFFFFF);
		background-size: 50px 100px;
		animation: dynamicBg 10s ease infinite;
  }
</style>



